h1 {
  text-shadow: 1px 1px 1px black;
  font-weight: normal;
}

body {
  background-image: url("../images/test.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  font-family: Helvetica, Arial, sans-serif;
  color: white;
  background-color: #003366;
}

.container {
  background-color: #003366;
}

.container .portfolio-image {
  display: inline-block;
  padding: 30px;
  max-width: 100%;
  height: auto;
}

.center-region {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 500px;
  background-color: rgba(0, 0, 0, 0.4);
  float: left;
}

.matthew-info {
  min-width: 40%;
  max-width: 65%;
  height: 80%;
  padding: 30px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
}

.matthew-info .text-content {
  border-left: 1px solid white;
  font-size: 2em;
  padding-left: 20px;
}

.matthew-info .text-content span {
  color: #FF9933;
}

.matthew-info .buttons {
  text-align: center;
}

.matthew-info .buttons a {
  border: none;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
  text-decoration: none;
  display: inline-block;
}

.matthew-info .buttons .one {
  background-color: #68B4FF;
}

.matthew-info .buttons .one:hover {
  color: white;
  background-color: rgba(255, 255, 255, 0.3);
}

.matthew-info .buttons .one:visited {
  color: white;
}

.matthew-info .buttons .two {
  background-color: transparent;
  border: 1px solid white;
}

.matthew-info .buttons .two:hover {
  color: white;
  background-color: rgba(255, 255, 255, 0.3);
}

.version {
  font-size: 14px;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.logo {
  margin-top: 15px;
}

.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

.navigation {
  height: 50px;
  background-color: #FF9933;
}

.navigation img {
  width: 35px;
}

.navigation ul {
  padding-left: 40px;
  padding-top: 7px;
  margin: 0;
}

.navigation ul li {
  padding: 10px;
  display: inline-block;
  border-radius: 5px;
}

.navigation ul li a {
  text-decoration: none;
  color: #0a2b50;
}

.navigation ul li:hover a {
  color: white;
}
