// Normalize Styles
@import 'node_modules/normalize.css/normalize';

// Import Modules
@import '../_modules/link/link';

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

h1 {
  text-shadow: 1px 1px 1px black;
  font-weight:normal;
}

body {

  background-image: url('../images/test.jpg');
  background-size: cover;
  background-repeat:no-repeat;
  font-family:Helvetica, Arial, sans-serif;
  color:white;
  background-color:#003366;
}

.container {
  background-color: #003366;
  .portfolio-image {
    display:inline-block;
    padding:30px;
    max-width:100%;
    height:auto;
  }


}

.center-region {

  width:100%;
  margin-left: auto;
  margin-right: auto;
  height:500px;
  background-color:rgba(0,0,0,0.4);
  float:left;


}

.matthew-info {
  min-width:40%;
  max-width:65%;
  height:80%;
  padding:30px;
  margin-top:40px;
  margin-left:auto;
  margin-right:auto;

  .text-content {
    border-left: 1px solid white;
    span {
      color:#FF9933;
    }
    font-size: 2em;
    padding-left: 20px;
  }

  .buttons {
    text-align:center;
    a {
      border:none;
      border-radius:5px;
      padding:10px;
      margin:10px;
      text-decoration:none;
      display:inline-block;

    }

    .one {
      background-color:#68B4FF ;


      &:hover {
        color: white;
        background-color:rgba(255,255,255,0.3);

      }
      &:visited {
        color:white;
      }

    }

    .two {
      background-color:transparent;
      border:1px solid white;
      &:hover {
        color: white;
        background-color:rgba(255,255,255,0.3);

      }
    }

  }
}

.version {
  font-size: 14px;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.logo {
  margin-top: 15px;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}


.navigation {
  height:50px;
  background-color:#FF9933;

  img {
    width:35px;
  }

  ul {
    padding-left:40px;
    padding-top: 7px;
    margin:0;

    li {
      a {
        text-decoration: none;
        color:#0a2b50;
      }
      padding: 10px;
      display:inline-block;
      border-radius:5px;
      &:hover {
        a {
          color:white;

        }
      }
    }
  }

}